import React from 'react';
import Header from '@/components/settings/header';
import Router, { useRouter } from 'next/router';
export default function HeyLayout({ children }: { children: React.ReactNode }) {
    const router= useRouter()
  return (

    <div className="max-h-screen bg-gray-50 mt-6 space-y-2">
              <Header propValue='Settings'/>
              <div className="bg-gray-50 px-6 py-4">
              <div className="flex items-center text-gray-600 mb-4">
                <button onClick={()=> router.push("./main/dashboard")} className="text-gray-600">
                  Settings
                </button>

              </div>
            </div>
              {children}
            </div>   
  );
}